"use client";

import React, { useState } from 'react';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { FSocietyCard } from '@/components/FSocietyCard';
import { toolkitData } from '@/lib/toolkitData';

export default function HomePage() {
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [selectedDifficulty, setSelectedDifficulty] = useState('all');

  const categories = ['all', ...Array.from(new Set(toolkitData.map(tool => tool.category)))];
  const difficulties = ['all', 'Beginner', 'Intermediate', 'Advanced'];

  const filteredTools = toolkitData.filter(tool => {
    const matchesSearch = tool.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         tool.description.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = selectedCategory === 'all' || tool.category === selectedCategory;
    const matchesDifficulty = selectedDifficulty === 'all' || tool.difficulty === selectedDifficulty;
    
    return matchesSearch && matchesCategory && matchesDifficulty;
  });

  return (
    <div className="min-h-screen bg-background">
      {/* Navigation */}
      <nav className="border-b border-border/40 bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-8">
              <h1 className="text-2xl font-bold text-foreground">
                FSociety Toolkit
              </h1>
              <div className="hidden md:flex space-x-6">
                <Link href="/" className="text-foreground hover:text-primary transition-colors">
                  Tools
                </Link>
                <Link href="/payload" className="text-muted-foreground hover:text-primary transition-colors">
                  Payload Generator
                </Link>
                <Link href="/mobile" className="text-muted-foreground hover:text-primary transition-colors">
                  Mobile Hacking
                </Link>
                <Link href="/wordlist" className="text-muted-foreground hover:text-primary transition-colors">
                  Wordlist Generator
                </Link>
                <Link href="/documentation" className="text-muted-foreground hover:text-primary transition-colors">
                  Documentation
                </Link>
              </div>
            </div>
            <div className="text-sm text-muted-foreground">
              Educational Security Platform
            </div>
          </div>
        </div>
      </nav>

      {/* Hero Section */}
      <section className="py-12 px-4">
        <div className="container mx-auto text-center">
          <pre className="bg-black text-green-400 p-6 rounded-lg font-mono text-lg max-w-4xl mx-auto mb-8 select-none">
            <span className="text-green-500">F</span>
            <span className="text-green-400">S</span>
            <span className="text-green-300">o</span>
            <span className="text-green-200">c</span>
            <span className="text-green-100">i</span>
            <span className="text-green-200">e</span>
            <span className="text-green-300">t</span>
            <span className="text-green-400">y</span>
            <span className="text-green-500"> - </span>
            <span className="text-green-600">T</span>
            <span className="text-green-700">o</span>
            <span className="text-green-800">o</span>
            <span className="text-green-900">l</span>
            <span className="text-green-800">k</span>
            <span className="text-green-700">i</span>
            <span className="text-green-600">t</span>
          </pre>
          <h2 className="text-4xl font-bold text-foreground mb-4">
            Cybersecurity Education Platform
          </h2>
          <p className="text-xl text-muted-foreground mb-8 max-w-2xl mx-auto">
            Learn about cybersecurity tools and techniques through interactive educational content. 
            Designed for security professionals, students, and researchers.
          </p>
          <div className="bg-yellow-500/10 border border-yellow-500/20 rounded-lg p-4 max-w-2xl mx-auto">
            <p className="text-sm text-yellow-400">
              ⚠️ Educational Use Only - All tools and information provided are for legitimate security research and learning purposes.
            </p>
          </div>
        </div>
      </section>

      {/* Filters Section */}
      <section className="py-8 px-4 bg-muted/20">
        <div className="container mx-auto">
          <div className="flex flex-col md:flex-row gap-4 items-center justify-between">
            <div className="flex flex-col sm:flex-row gap-4 w-full md:w-auto">
              <Input
                placeholder="Search tools..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full sm:w-64"
              />
              <Select value={selectedCategory} onValueChange={setSelectedCategory}>
                <SelectTrigger className="w-full sm:w-48">
                  <SelectValue placeholder="Category" />
                </SelectTrigger>
                <SelectContent>
                  {categories.map(category => (
                    <SelectItem key={category} value={category}>
                      {category === 'all' ? 'All Categories' : category}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <Select value={selectedDifficulty} onValueChange={setSelectedDifficulty}>
                <SelectTrigger className="w-full sm:w-48">
                  <SelectValue placeholder="Difficulty" />
                </SelectTrigger>
                <SelectContent>
                  {difficulties.map(difficulty => (
                    <SelectItem key={difficulty} value={difficulty}>
                      {difficulty === 'all' ? 'All Levels' : difficulty}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div className="text-sm text-muted-foreground">
              {filteredTools.length} tools found
            </div>
          </div>
        </div>
      </section>

      {/* Tools Grid */}
      <section className="py-12 px-4">
        <div className="container mx-auto">
          {filteredTools.length === 0 ? (
            <div className="text-center py-12">
              <p className="text-muted-foreground text-lg">
                No tools found matching your criteria.
              </p>
              <Button 
                onClick={() => {
                  setSearchTerm('');
                  setSelectedCategory('all');
                  setSelectedDifficulty('all');
                }}
                variant="outline"
                className="mt-4"
              >
                Clear Filters
              </Button>
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredTools.map((tool) => (
                <FSocietyCard key={tool.id} tool={tool} />
              ))}
            </div>
          )}
        </div>
      </section>

      {/* Footer */}
      <footer className="border-t border-border/40 py-8 px-4 mt-12">
        <div className="container mx-auto text-center">
          <p className="text-muted-foreground text-sm">
            FSociety Toolkit - Educational Cybersecurity Platform
          </p>
          <p className="text-muted-foreground text-xs mt-2">
            For educational and authorized security testing purposes only
          </p>
        </div>
      </footer>
    </div>
  );
}
